// External imports, installed libraries, etc
import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

// Internal data, functions, custom hooks, etc
import colors from '../../../styles/colors';
import breakpoints from '../../../styles/breakpoints';

// Internal components, images, etc
// NA ATM


const Button = styled.button`
	background: ${colors.primary[0]};
	border: 3px solid ${colors.primary[0]};
	color: ${colors.white};
	${props => props.variant === 'secondary' && (`
		background: ${colors.white};
		color: ${colors.primary[0]};
	`) }
	font-family: 'Biryani';
	font-weight: bold;
	padding: .9rem 1rem;
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
	transition: background-color .3s, border-color .3s, margin .3s;

	&:hover, &:focus {
		background: ${colors.primary.dark};
		border-color: ${colors.primary.dark};
		color: ${colors.white};
	}


	@media (min-width: ${breakpoints.mdUp}px) {
		/* Breakpoint smaller than SM */
		display: inline-block;
		min-width: 275px;
	}
`;

const Component = ( {children, className, onClick, title, target, variant} ) => {
	return (
		<Button className={className} onClick={onClick} title={title} target={target} variant={variant}>
			{children}
		</Button>
	);
}

export default Component;

Component.defaultProps = {
	
};

Component.propTypes = {
	children: PropTypes.any,
};
